@import "@/styles/main.scss";





















































    .footer-container{
        height: auto !important;
        width: 100%;
        background-color: #222222;
        display: flex;
        padding: 45px 49px 25px;
        justify-content: space-between;
        align-items: flex-start;

        .left-footer{
            display: flex;
            width: 66% !important;
            justify-content: space-between;
            align-items: flex-start;
        }
        .address-section{
            width: 50%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: space-between;

            .our-address{
                font-size: 0.95rem;
                margin-bottom: 20px;
                color: #999999;

            }

            .address-detail{
                font-size: 0.95rem;
                padding: 0;
                margin-bottom: 2px;
                color: lighten($color: #999999, $amount: 10) ;

            }
        }

        .contact-section{
            width: 50%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: space-between;

            .our-contacts{
                font-size: 0.95rem;
                margin-bottom: 20px;
                color: #999999;

            }

            .contacts-detail{
                font-size: 0.95rem;
                margin-bottom: 2px;
                color: lighten($color: #999999, $amount: 10) ;
            }
        }

        .social-media{
            width: 33%;
            display: flex;
            align-items: center;
            padding-top: 40px;
            justify-content: flex-end;

            .icon{
                width: 33px !important;
                height: 33px !important;
                padding: 6px;
                border: 2px solid #808080;
                margin-left: 35px;
                border-radius: 10px;
                color: #808080;
                transition:
                    background-color 0.2s ease-out,
                    transform 0.2s ease-out;

                &:hover{
                    background: #ffffff;
                    border: 2px solid #ffffff;
                    cursor: pointer;
                    transform: scale(1.2);

                }

                &:nth-child(1):hover{
                    color: #3A589B;
                }

                &:nth-child(2):hover{
                    color: #1C9CEB;
                }

                &:nth-child(3):hover{
                    color: #D42F88;
                }

                &:nth-child(4):hover{
                    color: #F70000;
                }

                &:nth-child(5):hover{
                    color: #25D366;
                }

                &:nth-child(5){
                    padding: 4px;

                }

                &:nth-child(3){
                    padding: 4px;

                }

                &:nth-child(4){
                    padding: 4px;

                }
            }


        }
    }

