@import "@/styles/main.scss";






















































































$spacer : 10px;
  .nav-links-div{
    z-index: 10000;
    padding-right: 0px;
  }

  .blur{
    background-color: rgba($color: #222222, $alpha: 0.5) !important;
    height: 60px !important;
  }

  .hamb-nav-links-div{
    position: absolute;
    width: 200px;
    display: none;
    right: 0;
    top: 0px;
    padding-top: 80px ;
    background-color: rgba($color: #fcfcfc, $alpha: 1);
    border-top-left-radius: 15px;
    border-bottom-left-radius: 10px;
    height: 1000px;
    z-index: 90 !important;
  }

  .hamb-nav-links-row{
    display: flex;
    width: 100%;
    justify-content: flex-start;
    padding: 5px 0 ;
    padding-left: 15px;
    border-bottom: 1px solid rgba($color: #000000, $alpha: 0.9);

    &:nth-child(1){
      border-top: 1px solid rgba($color: #000000, $alpha: 0.9);
    }

    &:hover{
      background-color: rgba($color: #000000, $alpha: 1);
      cursor: pointer;
    }
  }

  .hamburger-toggler{
    z-index: 2000;
    border: none;
    outline: none;
    background-color: transparent;
    transition: transform 0.4s;
    z-index: 91 !important;
    &:focus{
        outline:none;
    }
    .burger-icon{
      background-color: #ffffff;
      display: block;
      width: 35px;
      height: 4px !important;
      margin-bottom: 8px;
      position: relative;
      border-radius: 5px;
      z-index: 4;
      outline:none;

      &:nth-child(3){
        margin-bottom: 0px !important;

      }
      &:nth-child(2){
              height: 4px !important;
      }
    }
  }

  .hamb-nav-active{

    transform: translateX(-110px);
    transition: 0.3s;
    span:nth-of-type(1){
    transform: rotateZ(45deg) translateY(23px) translateX(10px);
    background-color: #000000 !important;
    outline: none;

    }

    span:nth-of-type(2){
      transform: translateX(200px);
    }

    span:nth-of-type(3){
        background-color: #000000 !important;
        transform: rotateZ(-45deg) translateY(-8px) translateX(-7px);

    }
  }

  .container-fluid{
    padding: 0 !important;
    // height: 0 !important;
    margin: 0 !important;
  }

  .nav-main-link-holder{

    margin-right: 28px !important;
    z-index: 5000;


  }

  .active-nav-link{
    color: #68E3F1 !important;
    border-radius: 10px !important;
    border: 2px solid #68E3F1;

  }

  .nav-main-link{
    text-align: left;
    font-size: 14px;
    font-weight: 600;
    height: 40px;
    display: flex;
    letter-spacing: 0rem !important;
    align-items: center;
    color: #ffffff ;
    text-decoration: none !important;
    padding: 10px;
    // border: 3px solid transparent;
    transition: all .2s ease;
    border-radius: 10px;
    z-index: 5000;
  }

  .nav-main-link:hover{

    color: #68E3F1 ;
    z-index: 5000;

  }

  .navbar{
    position: fixed;
    height: 95px;
    background-color: transparent;
    padding-top: 0 !important;
    padding-bottom: 0 !important;
    display: flex !important;
    align-items: center !important;
    top: 0px !important;
    width: 100%;
    z-index: 1000;

    .nav-logo{
      // margin-top: -5px;
      margin-left: 33px;
      z-index: 100000;

      .navbar-brand{
        height: 33px;
        margin: 0;
        padding: 0;
      }
      img{
        margin-top: -3px;
        z-index: 100000;

      }
    }
  }

  .hamburger-toggler{
    display: none;
  }

  @media (max-width: 650px) {
    .hamburger-toggler{
      display: block;
      margin-right:9px !important;

    }

    .nav-links-div{
      display: none !important;

    }

    .navbar{
      top: 25px !important;
      position: fixed !important;
      top: 0px !important;
      height: 55px !important;
      left: 0 !important;
    }
    .nav-logo{
      padding-left: 0 !important;
      margin-left:15px !important;
    }

    .active-nav-link{
    color: #000000 !important;
    border-radius: 10px !important;
    border: none !important;

    }

    .hamb-nav-links-row{
      height: 50px;
      padding: 0px 0!important;
    }

    div nav .hamb-nav-links-div{
      display: block !important;
      // padding-top: 10px !important;
      padding-bottom: 10px !important;

        .router-link-active{
            // background-color: rgba($color: #ffffff, $alpha: 0.9) !important;
        }
      .nav-main-link{
        width: 100% !important;
        height: 50px !important;
        padding-left: 20px !important;
        border-radius: 0 !important;
        color: #000000 !important;

        &:hover{
          color: #ffffff !important;
        }


      }
    }

  }
